.advantages{
    padding-top: 80px;
    padding-bottom: 115px;
    background-image: url('../../images/main-page/advantages-bg.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    & .container{
        position: relative;
    }
    &__title{
        width: 64%;
        margin: 0 auto;
        color: #FFF;
        line-height: 99%;
        text-align: center;
        font-family: Luckiest Guy;
        font-size: 100px;
        font-style: normal;
        font-weight: 400;
        letter-spacing: 10px;
        & span{
            font-size: 120px;
            color: #FFFE00;
        }
    }
    &__subtitle{
        color: #FFF;
        text-align: center;
        font-family: Luckiest Guy;
        font-size: 30px;
        width: 50%;
        margin: 25px auto 0 auto;
        font-style: normal;
        font-weight: 400;
        line-height: 40px; 
    }
    &__btn{
        margin-top: 40px;
        display: flex;
        justify-content: center;
        & button{
            border-radius: 10px;
            border: 4px solid #FFF;
            background: transparent;
            cursor: pointer;
            padding: 21px 30px;
            color: #FFF;
            font-family: Luckiest Guy;
            font-size: 30px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;

        }
    }
    &__items{
        display: flex;
        gap: 150px;
        justify-content: center;
        margin-top: 55px;
    }
    &__item{
        display: flex;
        align-items: center;
        gap: 30px;
        &__info{
            & span{
                color: #FFF;
                font-family: Luckiest Guy;
                font-size: 25px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;
            }
            & p{
                color: #FFF;
                font-family: Luckiest Guy;
                font-size: 40px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;
                letter-spacing: 4px;
            }
        }
    }
    & .gold-big{
        width: 267px;
        position: absolute;
        left: 43px;
        top: -8%;    
        & .gold{
            width: 100%;
        }
        & .star{
            &:nth-child(1){
                width: 37px;
                top: -5px;
                left: 72px;
            }
            &:nth-child(2){
                width: 26px;
                top: 71px;
                left: 167px;
            }
            &:nth-child(3){
                width: 37px;
                top: 122px;
                left: 131px;
            }
            &:nth-child(4){
                width: 37px;
                top: 173px;
                left: 19px;
            }
            &:nth-child(5){
                width: 19px;
                top: 12px;
                left: 205px;
                
            }
            &:nth-child(6){
                width: 13px;
                top: 63px;
                left: 224px;
            }
        }
    }
    & .gold-mini{
        width: 457px;
        position: absolute;
        top: 33%;
        right: -8px;
    
        & .gold{
            width: 100%;
        }
        & .star{
            &:nth-child(1){
                width: 49px;
                top: -54px;
                left: 123px;
            }
            &:nth-child(2){
                width: 17px;
                top: 1px;
                left: 231px;
            }
            &:nth-child(3){
                width: 49px;
                top: 190px;
                left: 96px;
            }
            &:nth-child(4){
                width: 49px;
                top: 109px;
                left: 300px;
            }
            &:nth-child(5){
                width: 16px;
                top: 109px;
                left: 382px;
                
            }
            &:nth-child(6){
                width: 23px;
                top: 164px;
                left: 414px;
            }
            &:nth-child(7){
                width: 33px;
                top: 284px;
                right: 13px;
            }
        }
    }
}