header{
    background: linear-gradient(180deg, #321F16 0%, rgba(50, 31, 22, 0.51) 100%);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    .header__inner{
        padding: 10px 0;
    }
    .header__logo{
        margin-right: 77px;
        & img{

            border-radius: 78px;
        }
    }
    .header-nav__list{
        margin-right: 108px;
    }
    .header-nav{
        display: flex;
        & ul{
            display: flex;
            gap: 32px;
            align-items: center;
            & li{
                list-style:none;
                & a{
                    color: #F0EDE0;
                    font-family: Luckiest Guy;
                    font-size: 30px;
                    text-decoration: none;
                    font-style: normal;
                    font-weight: 400;
                    line-height: normal;
                    transition: all .3s;
                    &:hover{
                        color: #9a9a9a;
                    }
                }
            }
        
        }
        & ul.header-nav__actions{
            
            & li {
                border-radius: 10px;
                border: 2px solid #FFF;
                background: linear-gradient(180deg, rgba(255, 255, 255, 0.30) 0%, rgba(255, 255, 255, 0.00) 100%);
                padding: 11px 14px;
                &:last-child{
                    padding: 11px 60px;
                }
                & a{
                    display: flex;
                    align-items: center;
                    gap: 10px;
                }
            }
        }
        
    }
    .header-nav__mobile{
        display: none;
    }
}
