*{
  margin: 0;
  padding:0;
  box-sizing: border-box;
}
html,body{
  overflow-x: hidden;
}
body {
  font-family: 'Luckiest Guy', sans-serif;
  font-weight: 400;
  // overflow-x: hidden;
}
.main{
  overflow: hidden;
  position: relative;
}
.container{
  max-width: 1798px;
  margin: 0 auto;
  padding: 0 15px;
}
.star{
  animation: starAnimation 2s infinite;
}
.diamond-item{
  // position: relative;
  // animation: diamondAnimation 5s ease infinite;
  &:nth-child(1){
    animation-delay: .5s;
  }
  &:nth-child(2){
    animation-delay: .7s;
  }
  &:nth-child(3){
    animation-delay: .3s;
  }
  &:nth-child(4){
    animation-delay: .6s;
  }
  & img.star{
    position: absolute;
  }
}
.gold-mini,
.gold-big{
  & img.star{
    position: absolute;
    &:nth-child(1){
      animation-delay: .5s;
    }
    &:nth-child(2){
      animation-delay: .7s;
    }
    &:nth-child(3){
      animation-delay: .3s;
    }
    &:nth-child(4){
      animation-delay: .6s;
    }
    &:nth-child(5){
      animation-delay: .2s;
    }
    &:nth-child(6){
      animation-delay: .4s;
    }
    &:nth-child(7){
      animation-delay: .6s;
    }
  }
}
@keyframes starAnimation {
  0%{
    opacity: 0;
  }
  50%{
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
}
@keyframes diamondAnimation {
  0%{
    transform: translateY(0);
  }
  50%{
    transform: translateY(30%);
  }
  100%{
    transform: translateY(0);
  }
}

